import React from 'react';
import HeartIcon from '../icons/basic_elaboration_message_heart.svg';

function Placeholder(props) {
    return (
        <React.Fragment>
            Osker p hur du ska tervinna ditt skrp?
            Anvnd skfunktionen eller <span class="bold" data-txt="karta" onClick={props.toggleView}>hitta nrmsta station.</span>
            <img
                className="margin-top-large center block"
                src={HeartIcon}
                alt="Ett hjrta i en pratbubbla"
            />
        </React.Fragment>
    );
}

export default Placeholder;



